export default {
    name: 'commentItem',
    props: {
        commentData: {
            type: Object,
            default () {
                return {}
            }
        }
    },
    data () {
        return {
            showMenu: false
        }
    },
    computed: {
    },
    methods: {
        changeShowMenu () {
            this.showMenu = !this.showMenu
        }
    },
}
